import React,{useEffect, useState} from 'react'
import '../App.css'
import { Typography, Button } from '@mui/material'
import { ethers } from 'ethers'
import { Web3Provider } from '@ethersproject/providers';


export const Banner = () => {

    const [isMetaMaskInstalled, setIsMetaMaskInstalled] = useState(false)
    const [isConnected, setIsConnected] = useState(false)
    const [balance, setBalance] = useState(0)
    const [address, setAddress] = useState('')

    useEffect(() => {
        if (window.ethereum || window.web3) {
          setIsMetaMaskInstalled(true);
          const provider = new Web3Provider(
            window.ethereum || window.web3.currentProvider
          );
      
          // Request access to the user's accounts
          window.ethereum.enable().then(() => {
            provider
              .getSigner()
              .getAddress()
              .then((res) => {
                // Return the address of the wallet
                console.log(res);
                if (res.length > 0) {
                  setIsConnected(true);
                  provider
                    .getBalance(res)
                    .then((balance) => {
                      setBalance(ethers.formatEther(balance));
                      setAddress(res);
                    })
                    .catch((err) => console.error(err));
                }
              })
              .catch((err) => console.error(err));
          });
        } else {
          console.log('MetaMask is not installed!');
        }
      }, []);
      
      
       
      
    

    return (
        <div className='flex flex-col m-auto h-[80%] border-2 border-black bg-gradient-to-r from-purple-500/75 to-pink-500/75 rounded-3xl w-[40%] md:w-[90%] sm:w-[90%]'>
          {isMetaMaskInstalled ? (
            isConnected ? (
              <div className='text-center text-4xl md:text-4xl sm:text-[24px] p-8'>
                <Typography className='font-fredoka' variant='p' color='#efefef' >
                  Wallet is now connected!
                </Typography>
                <div className='flex flex-col mt-28 text-2xl sm:text-xl md:text-2xl '>
                    <Typography className='font-fredoka' variant='p' color='#efefef' >
                        Current <span className='font-bold'>$MAGIC</span> balance: <span className='font-bold underline'>{balance}</span>
                    </Typography>
                </div>
                <div className='flex flex-col mt-4 text-xl md:text-xl sm:text-[12px]'>
                    <Typography className='font-fredoka' variant='p' color='#efefef' >
                        Current wallet address: <span className='underline'>{address}</span>
                    </Typography>
                </div>
                <div className='flex w-[50%] m-auto border-2 border-[#d71fed] mt-8 md:text-2xl sm:text-[1px]'>
                    <Button variant='outlined' color='secondary' sx={{color: '#efefef', width: '100%', padding: '4px'}}>
                        <a href='https://app.uniswap.org/#/swap?outputCurrency=0x2f25eA915811f23Be33e0910a2dc8Ff43E4Ce14A' className='font-fredoka' variant='p' color='#d71fed' >
                            Buy more <span className='font-bold'>$MAGIC</span> tokens
                        </a>
                    </Button>
                </div>
                <div className='w-[50%] m-auto border-2 border-[#38BCF9] mt-4 md:text-[32px] sm:text-[1px]'>
                    <Button variant='outlined' color='secondary' sx={{color: '#efefef', width: '100%', padding: '4px'}}>
                        <a href='https://t.me/magicai_official' className='font-fredoka' variant='p' color='#38BCF9' >
                            <span className='font-bold'>Telegram</span> channel
                        </a>
                    </Button>
                </div>
              </div>
            
            ) : (
              <div className='text-center text-4xl p-8 m-auto'>
                <Typography className='font-fredoka' variant='p' color='#efefef' >
                  Connecting your wallet...
                </Typography>
              </div>
            )
          ) : (
            <div className='text-center text-4xl p-8 m-auto'>
              <Typography className='font-fredoka' variant='p' color='#efefef' >
                MetaMask not installed
              </Typography>
            </div>
          )}
        </div>
      );
      
}